import React from "react";
import PropTypes from 'prop-types'
// @material-ui/core components
import { withStyles } from "@material-ui/core/styles";
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridItem from "../../components/Grid/GridItem.js";
import GridContainer from "../../components/Grid/GridContainer.js";
import Button from "../../components/CustomButtons/Button.js";
import Card from "../../components/Card/Card.js";
import CardHeader from "../../components/Card/CardHeader.js";
import CardAvatar from "../../components/Card/CardAvatar.js";
import CardBody from "../../components/Card/CardBody.js";
import CardFooter from "../../components/Card/CardFooter.js";
import CustomTabs from "../../components/CustomTabs/CustomTabs.js";
import Tasks from "../../components/Tasks/Tasks.js";
import { bugs, website, server } from "../../variables/general.js";
import avatar from "../../assets/images/qrlogo.svg";
import { Typography } from "@material-ui/core";
import { ImageDataUrls } from 'algorand-qrcode-react'

const { logo } = ImageDataUrls()

const styles = {
  gridItem:{
    display: 'flex'
  }
};



class AuthorizationExample extends React.Component {
 
  render(){
    const {classes} = this.props
    return (
      <div>
        <GridContainer>
          <GridItem xs={12} sm={12} md={8}>
            <CustomTabs
              headerColor="primary"
              tabs={[
                {
                  tabName: "lsig delegate",
                  tabContent: (
                      <Typography variant='h5' style={{marginTop: '5%',marginBottom: '5%',textAlign:'center'}}>COMING SOON ...</Typography>
                    ),
                },
                {
                  tabName: "lsig escrow",
                  tabContent: (
                      <Typography variant='h5' style={{marginTop: '5%',marginBottom: '5%',textAlign:'center'}}>COMING SOON ...</Typography>
                    ),
                },
                {
                  tabName: "lsig delegate offline",
                  tabContent: (
                      <Typography variant='h5' style={{marginTop: '5%',marginBottom: '5%',textAlign:'center'}}>COMING SOON ...</Typography>
                    ),
                },
                {
                  tabName: "lsig escrow offline",
                  tabContent: (
                      <Typography variant='h5' style={{marginTop: '5%',marginBottom: '5%',textAlign:'center'}}>COMING SOON ...</Typography>
                    ),
                },
              ]}
            />
          </GridItem>
          <GridItem xs={12} sm={12} md={4}>
            <Card profile>
              <CardAvatar profile>
                  <a href="#" onClick={(e) => e.preventDefault()}>
                    <img src={logo} alt="logo" />
                  </a>
              </CardAvatar>
              <CardBody profile>
                {/* <h6 className={classes.cardCategory}>CEO / CO-FOUNDER</h6>
                <h4 className={classes.cardTitle}>Alec Thompson</h4> */}
                <p className={classes.description}>
                </p>
                <Button color="primary" round>
                  Work in progress
                </Button>
              </CardBody>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}
AuthorizationExample.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired
}
export default withStyles(styles)(AuthorizationExample)