
import Dashboard from "@material-ui/icons/Dashboard";
//import Person from "@material-ui/icons/Person";
//import LibraryBooks from "@material-ui/icons/LibraryBooks";
import VerticalSplit from "@material-ui/icons/VerticalSplit";
import Receipt from "@material-ui/icons/Receipt";
import AccountBalanceWallet from "@material-ui/icons/AccountBalanceWallet";
//import AddToPhotos from "@material-ui/icons/AddToPhotos";
import VideogameAsset from "@material-ui/icons/VideogameAsset";
//import Notifications from "@material-ui/icons/Notifications";

import DashboardPage from "./views/Dashboard/Dashboard.js";
import AssetsExample from "./views/AssetsExample/AssetsExample.js";
import TransactionsExample from "./views/TransactionsExample/TransactionsExample.js";
import AuthorizationExample from "./views/AuthorizationExample/AuthorizationExample.js";
import SmartContractsExample from "./views/SmartContractsExample/SmartContractsExample.js";
import CustomizedContractsExample from "./views/CustomizedContractsExample/CustomizedContractsExample.js";


const dashboardRoutes = [
  {
    path: "/dashboard",
    name: "Home",  
    icon: Dashboard,
    component: DashboardPage,
    layout: "/algorandqrcodereact",
  },
  {
    path: "/transactions",
    name: "Payments QRCode Examples",

    icon: Receipt,
    component: TransactionsExample,
    layout: "/algorandqrcodereact",
  },
  {
    path: "/assets",
    name: "Assets QRCode Examples",
    icon: VideogameAsset,
    component: AssetsExample,
    layout: "/algorandqrcodereact",
  }, 
  {
    path: "/authorization",
    name: "Authorization(LogicSig) QRCode Examples",
    icon: AccountBalanceWallet,
    component: AuthorizationExample,
    layout: "/algorandqrcodereact",
  },
  {
    path: "/contracts",
    name: "Smart Contract Trx QRCode Examples",
    icon: VerticalSplit,
    component: SmartContractsExample,
    layout: "/algorandqrcodereact",
  },
  {
    path: "/customizedContracts",
    name: "Custom UseCase QRCode Examples",
    icon: VerticalSplit,
    component: CustomizedContractsExample,
    layout: "/algorandqrcodereact",
  }
  
  
];

export default dashboardRoutes;
