import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { ImageDataUrls } from 'algorand-qrcode-react'
import * as QRCode from 'algorand-qrcode/build/qrcode'
const { logo } = ImageDataUrls()
const styles = (theme) => ({
  qrcodeDiv: {
    borderbottom: '10px solid rgb(115, 175, 245)',
    border: '1px solid #333',
    margin: 'auto',
    display: 'block'
    // padding: 10
  },
  canvasWrapper:{
    display: 'none',
  },
  qrImage: {
    width: '256px',
    height: 'auto',
    margin: 'auto',
    [theme.breakpoints.down('xs')]: {
      width: '140px',
      height: '140px'
    }
  },
  assetUrl: {
    [theme.breakpoints.down('xs')]: {
      wordWrap: 'break-word'
    }
  },
  textDiv: {
    backgroundColor: '#ab47bc',
    borderColor: '#ab47bc',
    margin: 'auto',
    display: 'block'
  },
  subTitle: {
    padding: '10px',
    margin: 'auto',
    display: 'block'
  },
  uri: {
    padding: '10px',
    margin: 'auto',
    display: 'block',
    [theme.breakpoints.down('xs')]: {
      fontSize: '0.7em',
      wordBreak: 'break-word'
    }
  },

})
class SeconDeviceScanner extends Component {
  constructor() {
    super()
    this.state = {}
    
    
  }
  componentDidMount() {
    const self = this;
    /* const videoElem = document.getElementById('second-device-qrcode-scan-results-video') */
    var video = document.createElement("video");
    var canvasElement = document.getElementById("second-device-qrcode-scan-results-video");
    var canvas = canvasElement.getContext("2d");
    document.getElementById("canvas-wrapper").style.display= 'block'  

    function drawLine(begin, end, color) {
      canvas.beginPath();
      canvas.moveTo(begin.x, begin.y);
      canvas.lineTo(end.x, end.y);
      canvas.lineWidth = 4;
      canvas.strokeStyle = color;
      canvas.stroke();
    }

   
    navigator.mediaDevices.getUserMedia({ video: { facingMode: "environment" } }).then(function(stream) {
      self.qrstream = stream
      video.srcObject = self.qrstream;
      video.setAttribute("playsinline", true); 
      video.play();
      requestAnimationFrame(tick);
    });

    function tick() {
     
      if (video.readyState === video.HAVE_ENOUGH_DATA) {
    
        canvasElement.hidden = false;
  

        canvasElement.height = video.videoHeight;
        canvasElement.width = video.videoWidth;
        canvas.drawImage(video, 0, 0, canvasElement.width, canvasElement.height);
        var imageData = canvas.getImageData(0, 0, canvasElement.width, canvasElement.height);
        var code = QRCode.jsQR(imageData.data, imageData.width, imageData.height, {
          inversionAttempts: "dontInvert",
        });
        if (code) {
          drawLine(code.location.topLeftCorner, code.location.topRightCorner, "#FF3B58");
          drawLine(code.location.topRightCorner, code.location.bottomRightCorner, "#FF3B58");
          drawLine(code.location.bottomRightCorner, code.location.bottomLeftCorner, "#FF3B58");
          drawLine(code.location.bottomLeftCorner, code.location.topLeftCorner, "#FF3B58");
          self.props.printGeneratedQrcode(code.data)
        }
      }
      requestAnimationFrame(tick);
    }
    
  }
  componentWillUnmount(){
    if(this.qrstream)this.qrstream.getTracks().forEach(function(track) {
      track.stop();
  });
  }

  render() {
    const { classes } = this.props
    return (
      <div>
          <div className={classes.qrcodeDiv}>
            {/* <img  src={logo} className={classes.qrImage} /> */}
            {/* <video className={classes.qrImage} playsInline id="second-device-qrcode-scan-results-video"></video> */}
            <div id="canvas-wrapper" className={classes.canvasWrapper}>
              <canvas className={classes.qrImage} id="second-device-qrcode-scan-results-video" hidden></canvas>
            </div>

          </div>
          <div className={classes.textDiv}>
          <p>QR Code Scanner</p>
         
          </div>
        </div> 
    )
  }
}
SeconDeviceScanner.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired,
  printGeneratedQrcode: PropTypes.func.isRequired
}
export default withStyles(styles)(SeconDeviceScanner)
