/*eslint-disable*/
import React, { Component } from "react";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// nodejs library that concatenates classes
import classnames from "classnames";

import imagine1 from "../../assets/images/sidebar-1.jpg";
import imagine2 from "../../assets/images/sidebar-2.jpg";
import imagine3 from "../../assets/images/sidebar-3.jpg";
import imagine4 from "../../assets/images/sidebar-4.jpg";
import banner from "../../assets/images/algorand-qrcode-banner.jpg";
import generatedqrcodelogo from "../../assets/images/generated-qr.png";

import Button from "../../components/CustomButtons/Button.js";

export default function FixedPlugin(props) {
  const [classes, setClasses] = React.useState("dropdown show");
  const [bg_checked, setBg_checked] = React.useState(true);
  const [bgImage, setBgImage] = React.useState(props.bgImage);
  const handleClick = () => {
    props.handleFixedClick();
  };
  return (
    <div
      className={classnames("fixed-plugin")}
    >
      <div id="fixedPluginClasses" className={props.fixedClasses}>
        <div onClick={handleClick}>
          <img src={generatedqrcodelogo} style={{ padding: '5px', width: '32px', height: '32px', cursor: 'pointer'}} />
        </div>
        <ul className="dropdown-menu">
          <img src={banner} alt="Algorand QR Code React" />
          <li className="header-title-main">Algorand QR Code React Examples</li>
        
          <li className="button-container">
            <div className="button-container">
              <Button
                color="success"
                href="https://github.com/emg110/algorand-qrcode"
                target="_blank"
                fullWidth
              >
                Algorand QR Code Repository
              </Button>
            </div>
          </li>
          <li className="button-container">
            <div className="button-container">
              <Button
                color="warning"
                href="https://developer.algorand.org/solutions/algorand-qr-code-generator-javascript/"
                target="_blank"
                fullWidth
              >
                Algorand QR Code Published Article
              </Button>
            </div>
          </li>
          <li className="button-container">
            <Button
              color="danger"
              fullWidth
              href="https://www.youtube.com/watch?v=RzP3y42Lf4o"
              target="_blank"
            >
              Algorand Dev Hours Video
            </Button>
          </li>
          <li className="button-container">
            <Button
              color="info"
              fullWidth
              href="https://cutt.ly/SnkO7Xl"
              target="_blank"
            >
              Algorand Dev Hours Presentation file
            </Button>
          </li>
          {/* <li className="button-container">
            <Button
              color="primary"
              fullWidth
              href="/algorandqrcodereact/app"
              target="_self"
            >
              Go to simple React Demo
            </Button>
          </li> */}
          <li className="adjustments-line" />
         
          <li className="header-title">Theme Backgrounds</li>
          <li className={bgImage === imagine1 ? "active" : ""}>
            <a
              className="img-holder switch-trigger"
              onClick={() => {
                setBgImage(imagine1);
                props.handleImageClick(imagine1);
              }}
            >
              <img src={imagine1} alt="..." />
            </a>
          </li>
          <li className={bgImage === imagine2 ? "active" : ""}>
            <a
              className="img-holder switch-trigger"
              onClick={() => {
                setBgImage(imagine2);
                props.handleImageClick(imagine2);
              }}
            >
              <img src={imagine2} alt="..." />
            </a>
          </li>
          <li className={bgImage === imagine3 ? "active" : ""}>
            <a
              className="img-holder switch-trigger"
              onClick={() => {
                setBgImage(imagine3);
                props.handleImageClick(imagine3);
              }}
            >
              <img src={imagine3} alt="..." />
            </a>
          </li>
          <li className={bgImage === imagine4 ? "active" : ""}>
            <a
              className="img-holder switch-trigger"
              onClick={() => {
                setBgImage(imagine4);
                props.handleImageClick(imagine4);
              }}
            >
              <img src={imagine4} alt="..." />
            </a>
          </li>

        </ul>
      </div>
    </div>
  );
}

FixedPlugin.propTypes = {
  bgImage: PropTypes.string,
  handleFixedClick: PropTypes.func,
  rtlActive: PropTypes.bool,
  fixedClasses: PropTypes.string,
  bgColor: PropTypes.oneOf(["purple", "blue", "green", "orange", "red"]),
  handleColorClick: PropTypes.func,
  handleImageClick: PropTypes.func,
};
